@import "../styles/GeneralStyles.scss";

.main-wrapper-userdashboard {
  display: flex;
  flex-direction: column;
  margin: auto;
  align-items: center;
}

.sub-navbar {
  width: 100%;
  height: 80px;
  background-color: $quikenDarkBlue;
  display: flex;
  align-items: center;
  box-sizing: 20px;
  padding-left: 40px;
  padding-right: 40px;
  justify-content: space-evenly;
}

.link-subnavbar {
  font-size: 25px;
  flex: 1;
  font-family: $quikenFont;
  text-decoration: none;
  font-weight: 800;
  color: $quikenWhite;
  &:hover {
    color: $quikenRed;
  }
}

.account-name {
  font-family: $quikenFont;
  font-weight: 900;
  color: $quikenDarkBlue;
  align-self: flex-end;
  margin: auto;
  margin-bottom: 15px;
}

#blue {
  background-color: $quikenDarkBlue;
}

.div-row-wrapper {
  margin-top: 10px;
  width: 320px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.btn-cancel-guide {
  background-color: $quikenDarkBlue;
  color: $quikenWhite;
  font-weight: 900;
  border-radius: 10px;
  height: 25px;
  width: 90px;
}

.heading-userdashboards {
  color: $quikenRed;
  margin-top: 50px;
  font-family: $quikenFont;
  font-size: 32px;
  font-weight: 900;
  margin-bottom: 20px;
}

.div-main-cotizador-rapido {
  display: flex;
  flex-direction: row;
}

.column-cotizador-rapido {
  display: flex;
  flex-direction: column;
  padding: 0px 10px 0px 10px;
}

.cotizador-rapido-main-header {
  font-family: $quikenFont;
  color: $quikenDarkBlue;
  font-size: 28px;
  font-weight: 800;
}

.column-cotizador {
  display: flex;
  flex-direction: column;
  background-color: $quikenKLightGrey;
  padding: 30px;
  margin-bottom: 30px;
  margin-right: 20px;
  align-items: center;
  justify-content: center;
}

.main-cotizador-servicios {
  display: flex;
  flex-direction: row;
}

.información-paquete-subheading {
  font-family: $quikenFont;
  font-size: 18px;
  color: $quikenRed;
  font-weight: 800;
}

.label-paquete-info {
  margin-top: 10px;
  font-family: $quikenFont;
  color: $quikenDarkBlue;
  font-size: 13px;
  font-weight: 800;
}

.row-servicio-quota {
  display: flex;
}

.servicios-subheading-cotizador {
  font-family: $quikenFont;
  color: $quikenRed;
  font-family: 24px;
  font-weight: 800;
  font-size: 26px;
  margin-bottom: 15px;
}

.button-cotizar-servicios {
  font-size: 20px;
  font-family: $quikenFont;
  color: $quikenWhite;
  font-weight: 600;
  padding: 5px 30px 5px 30px;
  background-color: $quikenDarkBlue;
  border-radius: 20px;
  margin-top: 10px;
  justify-self: flex-end;
}

.servicios-paragraph-cotizador {
  max-width: 200px;
  font-family: $quikenFont;
  color: $quikenDarkBlue;
  font-weight: 600;
  text-align: center;
  font-size: 22px;
}

.seccion-desde-donde-envias {
  // background-color: $quikenLightBlue ;
  border: solid 2px $quikenDarkBlue;
  padding: 30px;
  border-radius: 25px;
  margin: 10px;
  padding-bottom: 50px;
}

.seccion-informacion-paquete {
  // background-color: $quikenLightBlue ;
  border: solid 2px $quikenDarkBlue;
  padding: 30px;
  border-radius: 25px;
  margin: 10px;
  padding-bottom: 50px;
}

.payment-main-wrapper {
  // background-color: $quikenLightBlue ;
  border: solid 2px $quikenDarkBlue;
  padding: 30px;
  border-radius: 25px;
  margin: 10px;
  padding-bottom: 50px;
}

.third-forth-sections {
  display: flex;
}

.buttons-wrapper-row {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  align-content: center;
  justify-content: center;
}

.table-row-heading {
  border: solid 2px black;
  background-color: $quikenDarkBlue;
  color: $quikenWhite;
  font-weight: 800;
}

.table-data-row {
  border: solid 2px $quikenKLightGrey;
  &:hover {
    background-color: $quikenRed;
  }
}

.cotizar-paragraph {
  font-family: $quikenFont;
  color: $quikenDarkBlue;
  font-weight: 600;
  font-size: 20px;
  text-align: center;
  margin-top: 10px;
}

.menu-opciones-saldo {
  height: 370px;
  background-color: $quikenKLightGrey;
  width: 600px;
}

.card-dashboard {
  height: 100%;
  background-color: $quikenWhite;
  width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  //border-radius: 5px;
  padding: 5px;
  //box-shadow: rgba(0, 0, 0, 0.20) 0px 5px 15px;
}

.card-row-wrapper {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 30%;
  margin-top: 20px;
}

.featured-number {
  font-size: 45px;
  font-family: $quikenFont;
  color: $quikenRed;
}

.wrapper-unauthorized-route {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40vh;
}

.unautohorized-heading {
  color: $quikenDarkBlue;
  font-weight: 800;
  font-size: 24px;
  font-family: $quikenFont;
}

.recargar-saldo-link {
  align-self: flex-end;
  font-size: 14px;
  margin-right: 15px;
  font: $quikenFont;
  font-weight: 600;
  color: $quikenDarkBlue;
  &:hover {
    color: $quikenRed;
  }
}

.recargar-saldo-link-trans {
  align-self: flex-end;
  font-size: 14px;
  margin-right: 15px;
  font: $quikenFont;
  font-weight: 600;
  color: $quikenWhite;
}

.link-sign-route {
  color: $quikenRed;
  font-size: 25px;
  margin-top: 10px;
  font-family: $quikenFont;
  font-weight: 800;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

.tabla-de-guias-heading {
  color: $quikenRed;
  margin-top: 50px;
  font-family: $quikenFont;
  font-size: 32px;
  font-weight: 900;
  margin-bottom: 20px;
}
